import uniPopup from '@/components/uni-popup/uni-popup.vue';
import uniPopupDialog from '@/components/uni-popup/uni-popup-dialog.vue';
import { getToken } from '@/utils/auth';
import { mapState } from 'vuex';
import { getJoinStatus, getProjectDetail } from '@/api/share.js';

export default {
	data() {
		return {
			showStatus: 0,
			projectData: {},
			inviteName:"陈一发儿"
		};
	},
	onLoad(options) {
		/* 初始化页面数据 */
		console.log( options )
		this.inviteName = options.inviteName ;
		getProjectDetail({
			projectId: options.projectId
		}).then(response => {
			let resData = response.data.data;
			let initData = {
				currentNum: resData.currentNum,
				endTime: resData.endTime,
				monthCount: resData.monthCount,
				monthDays: resData.monthDays,
				name: resData.name,
				pid: resData.pid,
				returnValue: resData.returnValue,
				startTime: resData.startTime,
				totalNum: resData.totalNum
			};
			this.projectData = initData;
		});
		/* 判断用户是否登录 */
		if (getToken()) {
			/* 获取用户与分享项目之间的状态 */
			getJoinStatus({
				projectId: options.projectId
			}).then(response => {
				let statusCode = response.data.data;
				switch (statusCode) {
					case 0:
						this.showStatus = 0;
						break;
					case 1:
						this.showStatus = 1;
						break;
					case 2:
						this.showStatus = 2;
						break;
					case 3:
						this.showStatus = 3;
						break;
					default:
						默认代码块;
				}
			});
		} else {
			this.showStatus = 4;
			this.$refs.popup.open();
		}
	},
	onShareAppMessage: function(optiom) {
		if (optiom.from === 'button') {
			// 来自页面内转发按钮
			return {
				title: '会子簿',
				desc: '专门记录会子的小程序',
				imageUrl: '../../static/images/cyf.jpg',
				path: '/pages/home/index?userName=' + this.userName + '&inviteId=' + 123,
				success: function(res) {
					// 转发成功
					console.log('转发成功:' + JSON.stringify(res));
				},
				fail: function(res) {
					// 转发失败
					console.log('转发失败:' + JSON.stringify(res));
				}
			};
		}
		return {
			title: '会子簿',
			desc: '专门记录会子的小程序',
			imageUrl: '../../static/images/cyf.jpg',
			path: '/pages/home/index?id=' + 123,
			success: function(res) {
				// 转发成功
				console.log('转发成功:' + JSON.stringify(res));
			},
			fail: function(res) {
				// 转发失败
				console.log('转发失败:' + JSON.stringify(res));
			}
		};
	},
	components: {
		uniPopup,
		uniPopupDialog
	},
	methods: {
		close(done) {
			done();
		},
		confirm(done) {
			uni.navigateTo({
				url: '/pages/login/index?from=share'
			});
			done();
		},
		toLogin() {
			this.$refs.popup.open();
		},
		join() {
			console.log('我加入会子了');
		}
	},
	filters: {
		formatDate: function(value) {
			var date = new Date(value); //时间戳为10位需*1000，时间戳为13位的话不需乘1000
			var Y = date.getFullYear() + '-';
			var M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '-';
			var D = date.getDate() + ' ';
			var h = date.getHours() + ':';
			var m = date.getMinutes() + ':';
			var s = date.getSeconds();
			return Y + M + D ;
		}
	}
};
